@import "../../../../../../assets/styles/foundations.scss";

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  &__bigText {
    font-family: "Roboto-Medium";
    font-weight: 500;
    font-size: calc(12.292rem + (1vw - 0.452rem));
    color: $neutral-900;

    animation: $showAnimation;
  }
}

/* font-size transition between (small window and hd) / (HD and full-HD) */
@media screen and (max-width: 2240px) {
  .container__bigText {
    font-size: calc(8.132rem + (1vw - 0.452rem));
  }
}

@media screen and (max-width: 1643px) {
  .container__bigText {
    font-size: calc(6.286rem + (1vw - 0.452rem));
  }
}

